<template>
  <Header :title="$t('language.title')" />
  <div class="wrap">
    <ul class="contents">
      <li class="language-select-area" v-for="(locale, index) in LOCALES" :key="index">
        <label>
          <input type="radio" :value="locale.value" v-model="$i18n.locale" />
          <span class="language">{{ locale.caption }}</span>
          <font-awesome-icon :icon="checkMark" class="check-icon" v-if="locale.value === $i18n.locale" />
        </label>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue'
import Header from '@/components/Header.vue'
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons'
import { LOCALES } from '../../local.config'

